


































































































































































































import Vue from 'vue';

export default Vue.component('Framework', {
  props: {
    LBlockChain: {
      type: String,
      default: '',
    },
    LMasterUser: {
      type: String,
      default: '',
    },
    LIAMUser: {
      type: String,
      default: '',
    },
    LSignOut: {
      type: String,
      default: '',
    },
    LScience: {
      type: String,
      default: '',
    },
    translateIcon: {
      type: String,
      default: 'mdi-translate',
    },
    openInNewIcon: {
      type: String,
      default: 'mdi-open-in-new',
    },
    cogIcon: {
      type: String,
      default: 'mdi-cog-outline',
    },
    signOutIcon: {
      type: String,
      default: 'mdi-logout-variant',
    },
    navigationItems: {
      type: Array,
      default() {
        return [
          {
            icon: 'mdi-home-import-outline',
            title: 'LHome',
            path: '',
          },
          {
            icon: 'mdi-link-lock',
            title: 'LChainLink',
            path: '',
          },
          {
            icon: 'mdi-shield-account-outline',
            title: 'LIAM',
            path: '',
          },
        ];
      },
    },
    functionItems: {
      type: Array,
      default() {
        return [
          {
            icon: 'mdi-account-outline',
            title: 'LMyUser',
            url: '',
          },
          {
            icon: 'mdi-wallet-outline',
            title: 'LMyBill',
            url: '',
          },
        ];
      },
    },
    footerIcons: {
      type: Array,
      default() {
        return [
          {
            name: 'mdi-wechat',
            href: '',
          },
          {
            name: 'mdi-sina-weibo',
            href: '',
          },
          {
            name: 'mdi-post',
            href: '',
          },
          {
            name: 'mdi-github',
            href: '',
          },
        ];
      },
    },
    account: {
      type: Object,
      default() {
        return {
          unionId: '',
          iam: '',
          username: '',
          email: '',
          phone: '',
          secret: '',
        };
      },
    },
    authenticated: {
      type: Array,
      default() {
        return [];
      },
    },
    signOut: {
      type: Function,
    },
  },
  data() {
    return {
      settingSelected: null,
      navigationDrawer: false,
    };
  },
  methods: {
    toggleLocale() {
      if (this.$i18n.locale === 'cn') {
        this.$store.commit('setLocale', 'en');
        this.$i18n.locale = 'en';
      } else {
        this.$store.commit('setLocale', 'cn');
        this.$i18n.locale = 'cn';
      }
    },
    toggleDarkMode() {
      this.$vuetify.theme.dark = !this.$vuetify.theme.dark;
    }
  },
});
