import Vue from 'vue';
import Vuex, {
  GetterTree, MutationTree, ActionTree, ActionContext, Module,
} from 'vuex';

import { providers, Contract, BigNumber } from 'ethers';
import detectEthereumProvider from '@metamask/detect-provider';

const { abi } = require('@/constants/ERC20.json');

Vue.use(Vuex);

export interface ERC20State {
  address?: string;
}

const getters: GetterTree<ERC20State, any> = {};
const mutations: MutationTree<ERC20State> = {
};

const provider = async (): Promise<any> => {
  return new providers.Web3Provider(await detectEthereumProvider() as any);
}

const signer = async(): Promise<any> => {
  return (await provider()).getSigner();
}

const actions: ActionTree<ERC20State, any> = {
  async balanceOf(context: ActionContext<ERC20State, any>, {contractAddress, accountAddress}: {contractAddress: string, accountAddress: string}): Promise<string> {
    const contract = new Contract(contractAddress, abi, await provider());
    return (await contract.balanceOf(accountAddress)).toString();
  },
  async approve(context: ActionContext<ERC20State, any>, {contractAddress, spenderAddress, amount}: {contractAddress: string, spenderAddress: string, amount: string}): Promise<any> {
    const contract = new Contract(contractAddress, abi, await signer());
    return contract.approve(spenderAddress, amount);
  },
  async allowance(context: ActionContext<ERC20State, any>, {contractAddress, ownerAddress, spenderAddress}: {contractAddress: string, ownerAddress: string, spenderAddress: string}): Promise<any> {
    const contract = new Contract(contractAddress, abi, await provider());
    return contract.allowance(ownerAddress, spenderAddress);
  },
  async transfer(context: ActionContext<ERC20State, any>, {contractAddress, recipientAddress, amount}: {contractAddress: string, recipientAddress: string, amount: string}): Promise<any> {
    const contract = new Contract(contractAddress, abi, await signer());
    return contract.transfer(recipientAddress, amount);
  },
  convertNumber(context: ActionContext<ERC20State, any>, {value, decimals}: {value: string, decimals?: number}) {
    const decimalLength = decimals || 18;
    console.log(value.length);
    console.log(value);
    if (value.length > decimalLength) {
      const decimals = value.slice(-1 * decimalLength);
      const integer = value.slice(0, -1 * decimalLength);
      return `${integer}.${decimals}`;
    } else {
      return `0.${value.concat('0'.repeat((decimalLength || 18) - value.length), value)}`
    }
  }
};

export const erc20: Module<ERC20State, any> = {
  namespaced: true,
  state: {},
  getters,
  mutations,
  actions,
};
