import Vue from 'vue';
import Vuex, {
  GetterTree, MutationTree, ActionTree, ActionContext, Module,
} from 'vuex';

import { providers, Contract, BigNumber } from 'ethers';
import detectEthereumProvider from '@metamask/detect-provider';

const { abi } = require('@/constants/IMdexRouter.json');

Vue.use(Vuex);

export interface MdexRouter {
  address?: string;
}

const getters: GetterTree<MdexRouter, any> = {};
const mutations: MutationTree<MdexRouter> = {};

const provider = async (): Promise<any> => {
  return new providers.Web3Provider(await detectEthereumProvider() as any);
};

const actions: ActionTree<MdexRouter, any> = {
  async getAmountsOut(context: ActionContext<MdexRouter, any>, {
    contractAddress,
    amountIn,
    path,
  }: { contractAddress: string, amountIn: string, path: string[] }): Promise<string> {
    const contract = new Contract(contractAddress, abi, await provider());
    return (await contract.getAmountsOut(amountIn, path)).toString();
  },
  getAmountOutMin(context: ActionContext<MdexRouter, any>, {
    amountOut,
    slipPoint,
  }: { amountOut: string, slipPoint: string }): string {
    return BigNumber.from(amountOut).mul(1000 - parseFloat(slipPoint) * 10).div(1000).toString();
  },

  // TODO: Refactor this section after routing algorithm is out
  async pippiGetAmountsOut(context: ActionContext<MdexRouter, any>, {
    contractAddress,
    amountIn,
    path,
  }: {
    contractAddress: string,
    amountIn: string,
    path: string[],
  }): Promise<string> {
    console.log({
      contractAddress,
      amountIn,
      path,
    })
    const { abi } = require('@/constants/PiPPiRouter.json');
    const contract = new Contract(contractAddress, abi, await provider());
    return (await contract.getAmountsOut(amountIn, path)).toString();
  },
  async dogeGetAmountsOut(context: ActionContext<MdexRouter, any>, {
    contractAddress,
    amountIn,
    path,
  }: {
    contractAddress: string,
    amountIn: string,
    path: string[],
  }): Promise<string> {
    const { abi } = require('@/constants/DogeRouter.json');
    const contract = new Contract(contractAddress, abi, await provider());
    return (await contract.getAmountsOut(amountIn, path)).toString();
  },
  async lavaGetAmountsOut(context: ActionContext<MdexRouter, any>, {
    contractAddress,
    amountIn,
    path,
  }: {
    contractAddress: string,
    amountIn: string,
    path: string[]
  }): Promise<string> {
    const { abi } = require('@/constants/LavaRouter.json');
    const contract = new Contract(contractAddress, abi, await provider());
    return (await contract.getAmountsOut(amountIn, path)).toString();
  },
  async BXHGetAmountsOut(context: ActionContext<MdexRouter, any>, {
    contractAddress,
    amountIn,
    path,
  }: {
    contractAddress: string,
    amountIn: string,
    path: string[]
  }): Promise<string> {
    const { abi } = require('@/constants/BXHRouter.json');
    const contract = new Contract(contractAddress, abi, await provider());
    return (await contract.getAmountsOut(amountIn, path)).toString();
  }
};

export const mdexRouter: Module<MdexRouter, any> = {
  namespaced: true,
  state: {},
  getters,
  mutations,
  actions,
};
