import Vue from 'vue';
import VueRouter, { RouteConfig } from 'vue-router';
import Home from '@/views/Home.vue';

Vue.use(VueRouter);

const routes: Array<RouteConfig> = [
  {
    path: '/assets',
    name: 'Home',
    component: Home,
  },
  {
    path: '/',
    name: 'Swap',
    component: () => import('../views/Swap.vue'),
  },
  {
    path: '/mining',
    name: 'Mining',
    component: () => import('../views/Mining.vue'),
  },
  {
    path: '/explorer',
    name: 'Explorer',
    component: () => import('../views/Explorer.vue'),
    children: [
      {
        path: '/explorer/',
      },
    ],
  },
  {
    path: '/explorer/tx/:hash',
    name: 'Transaction',
    component: () => import('../views/Transaction.vue'),
  },
  {
    path: '/explorer/contract/:hash',
    name: 'ContractTxList',
    component: () => import('../views/ContractTxList.vue'),
  },
  {
    path: '/explorer/token/:hash',
    name: 'TokenTxList',
    component: () => import('../views/TokenTxList.vue'),
  },
  {
    path: '/404',
    name: 'NotFound',
    component: () => import('../views/NotFound.vue'),
  },
  {
    path: '/redirect',
    beforeEnter(to, from, next) {
      window.location.href = 'https://chocoswap.finance/';
    },
  },
];

const router = new VueRouter({
  base: process.env.BASE_URL,
  routes,
});

export default router;
