





































import Vue from 'vue';
import { mapActions, mapState } from 'vuex';
import ContractList from '@/constants';
import { fixTo8, fromInteger2Float } from '@/libs';

export default Vue.extend({
  data() {
    return {
      headers: [
        {
          value: 'icons',
        },
        {
          text: this.$i18n.t('IndexCoin'),
          align: 'start',
          value: 'coins',
        },
        {
          text: this.$i18n.t('IndexBalance'),
          value: 'balance',
          align: 'end'
        },
      ],
      assets: [] as any,
      loaded: false,
    };
  },
  computed: {
    ...mapState('wallet', [
      'accounts',
      'chainId'
    ]),
    ...mapState('swap', [
      'balances',
    ])
  },
  async created() {
    await this.connect();
    await this.fetchBalances();

    // initialize token list
    for (const coin of Object.keys(ContractList[this.chainId])) {
      this.assets.push({
        coins: coin,
        balance: fixTo8(this.balances[coin]),
      });
    }

    this.loaded = true;
  },
  methods: {
    ...mapActions({
      fetchBalances: 'swap/fetchBalances',
      connect: 'wallet/connect',
    }),
  },
});
