import {
  eth,
  heco,
} from '@/utils/request';
import { Condition, Transaction } from '@/interfaces/explorer';
import Constants from '@/constants';

export const getTransactionByHash = (hash: string) => {
  return eth.request({
    url: `/eth/txs/${hash}`,
    method: 'get',
  });
};

export const getTransactions = (data: Condition) => {
  return eth.request({
    url: '/eth/txs',
    method: 'post',
    data,
  });
};

export const getTotalSupply = (address: string) => {
  return eth.request({
    url: `/heco/erc20/${address}/totalSupply`,
    method: 'get',
  });
};
