import Vue from 'vue';
import Vuex, { ActionContext, ActionTree, GetterTree, Module, MutationTree } from 'vuex';

import { Condition, Token, Transaction } from '@/interfaces/explorer';
import Constants from '@/constants';
import { getTotalSupply, getTransactionByHash, getTransactions } from '@/api/eth';
import { fromInteger2Float } from '@/libs';

Vue.use(Vuex);

export interface ExplorerState {
  address?: string;
  tx?: Transaction;
  tokens?: Array<Token>;
}

const getters: GetterTree<ExplorerState, any> = {};
const mutations: MutationTree<ExplorerState> = {};
const actions: ActionTree<ExplorerState, any> = {
  async getTransactionByHash(context: ActionContext<ExplorerState, any>, hash: string) {
    return getTransactionByHash(hash);
  },
  async getTransactions(context: ActionContext<ExplorerState, any>, condition: Condition) {
    const txs = getTransactions(condition);
    if (txs) {
      return txs;
    }
    throw new Error(`Bad Transactions: ${txs}`);
  },
  async getTotalSupply(context: ActionContext<ExplorerState, any>, address: string) {
    const totalSupply = getTotalSupply(address);
    if (totalSupply) {
      return totalSupply;
    }
  },
  async fetchTokenList(context: ActionContext<ExplorerState, any>) {
    const { dispatch, state } = context;

    if (state.tokens?.length === 0) {
      for (const name of Object.keys(Constants['0x80'])) {
        const address = Constants['0x80'][name]['address'];
        const totalSupply = parseFloat(
          fromInteger2Float(
            await dispatch('getTotalSupply', address),
            Constants['0x80'][name]['decimal'],
          ),
        )
          .toFixed(3)
          .toString();

        state.tokens.push({
          name,
          address,
          totalSupply,
        });
      }
    }
  },
};

export const explorer: Module<ExplorerState, any> = {
  namespaced: true,
  state: {
    tokens: [],
  },
  getters,
  mutations,
  actions,
};
