



















































import Vue from 'vue';
import { mapActions, mapState } from 'vuex';

import Framework from '@/components/framework/Framework.vue';
import ContractList from './constants';
import Preference from '@/components/preference/Preference.vue';

import i18n from '@/locale';

export default Vue.extend({
  name: 'App',

  components: {
    Framework,
    Preference,
  },

  data: () => ({
    label: {
      drawer: {
        index: i18n.t('NavHome'),
        transaction: i18n.t('NavSwap'),
        mining: i18n.t('NavMining'),
        explorer: i18n.t('NavExplorer'),
      },
    },
    authenticated: ['Home', 'Transaction', 'Mining'],
    chainName: '',
    navigation: [
      {
        icon: 'mdi-swap-horizontal-bold',
        title: 'NavSwap',
        path: '/',
      },
      {
        icon: 'mdi-earth',
        title: 'NavExplorer',
        path: '/explorer',
      }
    ],
    snackbar: false,
    snackbarText: '',
    snackbarTimeout: 3000,
    overlay: false,
  }),
  computed: {
    ...mapState({
      accounts: (state: any) => state.wallet.accounts,
      connected: (state: any) => state.wallet.connected,
      chainId: (state: any) => state.wallet.chainId,
      unlocked: (state: any) => state.wallet.unlocked,
      authorized: (state: any) => state.wallet.authorized,
    }),
  },
  methods: {
    ...mapActions({
      connect: 'wallet/connect',
      disconnect: 'wallet/disconnect',
      connectWallet: 'wallet/connectWallet',
      addEthereumChainParameter: 'wallet/addEthereumChainParameter',
    }),
    showSnackbar() {
      if (this.accounts[0]) {
        console.log('chainId: ', this.chainId);
        this.snackbar = true;
        if (this.chainId !== '0x80') {
          this.snackbarText = this.$t('AppWrongNetwork').toString();
        } else {
          this.snackbarText = this.$i18n.t('AppRightNetwork').toString();
        }
      }

    },
  },
  async mounted() {
    await this.addEthereumChainParameter();
    if (this.authorized) {
      await this.connect();
    }
    if (this.chainId) {
      this.chainName = ContractList.chain[this.chainId];
      console.log('App Launching: ', this.chainId !== '0x80');
      if (this.chainId !== '0x80') {
        this.overlay = true;
      }
    }
  }
});
