import Vue from 'vue';
import VueI18n from 'vue-i18n';

import store from '@/store';

import cn from './zh-cn';
import en from './en-us';

Vue.use(VueI18n);

const locale = store.state.locale || 'cn';
const messages = {
  cn,
  en,
};

export default new VueI18n({
  locale,
  messages,
});
