import Vue from 'vue';
import Vuex, { ActionContext, ActionTree, GetterTree, Module, MutationTree } from 'vuex';

import { Contract, providers } from 'ethers';
import detectEthereumProvider from '@metamask/detect-provider';

import { DirectedGraph } from 'graphology';

import Constants from '@/constants';
import { compare, computeFees, fixTo8, fromFloat2Integer, fromInteger2Float } from '@/libs';
import dijkstra from 'graphology-shortest-path/dijkstra';

const { abi } = require('@/constants/WrappedSwap.json');

Vue.use(Vuex);

export interface SwapState {
  address?: string;
  slipPoint: string;
  deadline: string;

  tokenIn: string;
  tokenOut: string;

  balanceIn: string;
  balanceOut: string;

  amountIn: string;
  amountOut: string;

  amountOutMin: string;
  exchangeRate: string;
  txFee: string;
  isApproved: boolean;

  balances: any;
  tokenGraph: DirectedGraph;
}

const provider = async (): Promise<any> => {
  return new providers.Web3Provider((await detectEthereumProvider()) as any);
};

const signer = async (): Promise<any> => {
  return (await provider()).getSigner();
};

const getters: GetterTree<SwapState, any> = {
  getSlipPoint(state: SwapState): string | undefined {
    return state.slipPoint;
  },
};
const mutations: MutationTree<SwapState> = {
  setSlipPoint(state: SwapState, custom: string) {
    if (custom && custom !== '') {
      state.slipPoint = custom;
    }
  },
  setDeadline(state: SwapState, deadline: string) {
    state.deadline = deadline;
  },
  setTokenIn(state: SwapState, tokenIn: string) {
    state.tokenIn = tokenIn;
  },
  setTokenOut(state: SwapState, tokenOut: string) {
    state.tokenOut = tokenOut;
  },
  setAmountIn(state: SwapState, amountIn: string) {
    state.amountIn = amountIn;
  },
  resetAmountOut(state: SwapState) {
    state.amountOut = '0';
  },
};

const actions: ActionTree<SwapState, any> = {
  async swap(context: ActionContext<SwapState, any>) {
    const { rootState, state } = context;

    const contract = new Contract(Constants['swap'][rootState.wallet.chainId], abi, await signer());

    return await contract.swapExactTokensForTokens(
      fromFloat2Integer(state.amountIn, Constants[rootState.wallet.chainId][state.tokenIn].decimal),
      fromFloat2Integer(
        state.amountOutMin,
        Constants[rootState.wallet.chainId][state.tokenOut].decimal,
      ),
      getAddress(
        Constants['pairs'][state.tokenIn][state.tokenOut].path[0],
        rootState.wallet.chainId,
      ),
      rootState.wallet.accounts[0],
      fromFloat2Integer(
        (Math.floor(Date.now() / 1000) + parseInt(state.deadline || '20') * 60).toString(),
        18,
      ),
    );
  },

  async router(
    context: ActionContext<SwapState, any>,
    { contractAddress }: { contractAddress: string },
  ) {
    const contract = new Contract(contractAddress, abi, await provider());
    return await contract.router();
  },

  async takerWithdraw(
    context: ActionContext<SwapState, any>,
    { contractAddress }: { contractAddress: string },
  ) {
    const contract = new Contract(contractAddress, abi, await signer());
    return await contract.rewardWithdraw();
  },

  async getReward(
    context: ActionContext<SwapState, any>,
    {
      contractAddress,
      userAddress,
      tokenAddress,
    }: { contractAddress: string; userAddress: string; tokenAddress: string },
  ) {
    const contract = new Contract(contractAddress, abi, await provider());
    return (await contract.getReward(userAddress, tokenAddress)).toString();
  },

  async getSwapAmount(
    context: ActionContext<SwapState, any>,
    {
      contractAddress,
      userAddress,
      tokenAddress,
    }: { contractAddress: string; userAddress: string; tokenAddress: string },
  ) {
    const contract = new Contract(contractAddress, abi, await provider());
    return (await contract.getSwapAmount(userAddress, tokenAddress)).toString();
  },

  async getApprove(context: ActionContext<SwapState, any>) {
    const { state, rootState, dispatch } = context;

    if (rootState.wallet.accounts[0]) {
      const allowance = await dispatch(
        'erc20/allowance',
        {
          contractAddress: Constants[rootState.wallet.chainId][state.tokenIn].address,
          ownerAddress: rootState.wallet.accounts[0],
          spenderAddress: Constants['swap'][rootState.wallet.chainId],
        },
        { root: true },
      );

      state.isApproved = compare(allowance, Constants.allowance.slice(0, -1)) >= 0;
    }
  },

  async getExchangeRate(context: ActionContext<SwapState, any>, { reverse }: { reverse: boolean }) {
    const { rootState, state, dispatch } = context;

    const amountOut = (
      await dispatch(
        'mdexRouter/getAmountsOut',
        {
          contractAddress: Constants['swap']['mdex'],
          amountIn: fromFloat2Integer(
            '1',
            Constants[rootState.wallet.chainId][state.tokenIn].decimal,
          ),
          path: getAddress(
            Constants['pairs'][state.tokenIn][state.tokenOut].path[0],
            rootState.wallet.chainId,
          ),
        },
        { root: true },
      )
    ).split(',');

    const floatAmountOut = parseFloat(
      fromInteger2Float(
        amountOut[amountOut.length - 1],
        Constants[rootState.wallet.chainId][state.tokenOut].decimal,
      ),
    );

    if (!reverse) {
      state.exchangeRate = fixTo8(floatAmountOut.toString());
    } else {
      state.exchangeRate = fixTo8((1 / floatAmountOut).toString());
    }
  },

  async getAmountOut(context: ActionContext<SwapState, any>) {
    const { rootState, state, dispatch } = context;

    if (state.amountIn !== '') {
      const result = await dispatch(
        'mdexRouter/getAmountsOut',
        {
          contractAddress: Constants['swap']['mdex'],
          amountIn: fromFloat2Integer(
            state.amountIn,
            Constants[rootState.wallet.chainId][state.tokenIn].decimal,
          ),
          path: getAddress(
            Constants['pairs'][state.tokenIn][state.tokenOut].path[0],
            rootState.wallet.chainId,
          ),
        },
        { root: true },
      );

      if (result) {
        const amountsOut = result.split(',');
        state.amountOut = fromInteger2Float(
          amountsOut[amountsOut.length - 1],
          Constants[rootState.wallet.chainId][state.tokenOut].decimal,
        );
      }
    }
  },

  async getTxFee(context: ActionContext<SwapState, any>) {
    const { state, rootState } = context;

    if (state.amountIn && state.amountIn !== '') {
      const decimals = Constants[rootState.wallet.chainId][state.tokenIn].decimal;
      const compound = Constants['pairs'][state.tokenIn][state.tokenOut].path[0].length;
      state.txFee = fromInteger2Float(
        computeFees(fromFloat2Integer(state.amountIn, decimals), Constants['txFeeRate'], compound),
        decimals,
      );
      console.log('txFee(): ', state.txFee);
    }
  },

  async getMinIncome(context: ActionContext<SwapState, any>) {
    const { state, rootState, dispatch } = context;

    state.amountOutMin = fixTo8(
      fromInteger2Float(
        await dispatch(
          'mdexRouter/getAmountOutMin',
          {
            amountOut: fromFloat2Integer(
              state.amountOut,
              Constants[rootState.wallet.chainId][state.tokenOut].decimal,
            ),
            slipPoint: state.slipPoint,
          },
          { root: true },
        ),
        Constants[rootState.wallet.chainId][state.tokenOut].decimal,
      ),
    );
  },

  async getBalance(context: ActionContext<SwapState, any>) {
    const { dispatch, state, rootState } = context;

    try {
      state.balanceIn = fromInteger2Float(
        await dispatch(
          'erc20/balanceOf',
          {
            contractAddress: Constants[rootState.wallet.chainId][state.tokenIn].address,
            accountAddress: rootState.wallet.accounts[0],
          },
          { root: true },
        ),
        Constants[rootState.wallet.chainId][state.tokenIn].decimal,
      );

      state.balanceOut = fromInteger2Float(
        await dispatch(
          'erc20/balanceOf',
          {
            contractAddress: Constants[rootState.wallet.chainId][state.tokenOut].address,
            accountAddress: rootState.wallet.accounts[0],
          },
          { root: true },
        ),
        Constants[rootState.wallet.chainId][state.tokenOut].decimal,
      );
    } catch (e) {
      console.error('getBalance(): ', e);
    }
  },

  async fetchBalances(context: ActionContext<SwapState, any>) {
    const { dispatch, rootState, state } = context;

    for (const token of Object.keys(Constants[rootState.wallet.chainId])) {
      await dispatch(
        'erc20/balanceOf',
        {
          contractAddress: Constants[rootState.wallet.chainId][token].address,
          accountAddress: rootState.wallet.accounts[0],
        },
        { root: true },
      ).then((result) => {
        console.log(token, result);
        state.balances[token] = fromInteger2Float(
          result,
          Constants[rootState.wallet.chainId][token].decimal,
        );
      });
    }
  },

  // TODO: wrong edge generation algorithm
  async initGraph(context: ActionContext<SwapState, any>) {
    const { rootState, state, dispatch } = context;

    const chainId = rootState.wallet.chainId;

    state.tokenGraph = new DirectedGraph();
    for (let token of Object.keys(Constants[chainId])) {
      state.tokenGraph.addNode(token);
    }

    state.tokenGraph.forEachNode((key, _attr) => {
      state.tokenGraph.forEachNode(async (subKey, _subAttr) => {
        if (key !== subKey) {
          const amountsOut = (
            await dispatch(
              'mdexRouter/getAmountsOut',
              {
                contractAddress: Constants['swap']['mdex'],
                amountIn: fromFloat2Integer('1', Constants[chainId][key].decimal),
                path: [Constants[chainId][key].address, Constants[chainId][subKey].address],
              },
              { root: true },
            )
          ).split(',');
          const weight =
            fromInteger2Float(
              amountsOut[amountsOut.length - 1],
              Constants[chainId][subKey].decimal,
            ) || 0;
          // console.warn('initGraph(): ', key, subKey, weight);
          state.tokenGraph.addEdge(key, subKey, {
            weight,
          });
        }
      });
    });

    console.log('Graph Init Complete');
  },

  async shortestPath(
    context: ActionContext<SwapState, any>,
    { amountIn, amountOut }: { amountIn: string; amountOut: string },
  ) {
    const { state } = context;

    const path = dijkstra.bidirectional(state.tokenGraph, amountIn, amountOut, 'weight');

    console.warn(path);
  },
};

export const swap: Module<SwapState, any> = {
  namespaced: true,
  state: {
    slipPoint: '0.1',
    deadline: '20',
    tokenIn: 'VNLA',
    tokenOut: 'MDX',
    balanceIn: '',
    balanceOut: '',
    amountIn: '',
    amountOut: '',
    amountOutMin: '',
    exchangeRate: '...',
    txFee: '0',

    isApproved: false,
    balances: {},
    tokenGraph: new DirectedGraph(),
  },
  actions,
  getters,
  mutations,
};

function getAddress(tokens: Array<string>, chainId: string): Array<string> {
  const path: Array<string> = [];
  for (const token of tokens) {
    path.push(Constants[chainId][token].address);
  }
  return path;
}
