































































































import Vue from 'vue';
import Constants from '@/constants';
import i18n from '@/locale';
import { mapMutations } from 'vuex';

export default Vue.extend({
  name: 'Preference',
  data() {
    return {
      menu: false,
      slipPoints: Constants.slipPoints,
      customSlipPoint: '',
      slipPointChipIndex: 0,
      deadline: '',

      rules: {
        positiveNumber: (value: string = '') => {
          // positive float or positive integer
          const localValue: string = value.toString();
          if (localValue.match(/^[1-9]\d*\.\d*|0\.\d*[1-9]\d*$/) || localValue.match(/^[1-9]\d*$/) || localValue == '') {
            return true;
          } else {
            return i18n.t('SwapInputWarning');
          }
        },
        positiveInteger: (value: string = '') => {
          const localValue: string = value.toString();
          if (localValue.match(/^[1-9]\d*$/) || localValue === '') {
            return true;
          } else {
            return i18n.t('SwapTxDueWarning');
          }
        },
        lengthLimit: (value: string = '') => {
          const localValue: string = value.toString();
          if (localValue.length < 25) {
            return true;
          } else {
            return i18n.t('SwapInputExceedLengthWarning');
          }
        },
      }
    };
  },
  watch: {
    slipPointChipIndex: function (val: number) {
      if (this.customSlipPoint === '') {
        const slipPoint = this.slipPoints[val];
        console.log(val);
        this.setSlipPoint(slipPoint);
      }
    },
    customSlipPoint: function(val: number) {
      this.setSlipPoint(val);
    },
    deadline: function(val) {
      this.setDeadline(val);
    }
  },
  methods: {
    ...mapMutations({
      setSlipPoint: 'swap/setSlipPoint',
      setDeadline: 'swap/setDeadline',
    })
  }
});
